html,
body {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  color: #333;
  background: #fff;
}
.wrap {
  margin: 0 auto;
  padding: 0 50px;
  min-width: 480px;
  max-width: 960px;
}
h1 {
  font-weight: 400;
  font-size: 35px;
  text-align: center;
}
label {
  display: block;
  font-weight: 300;
  font-size: 30px;
  text-align: center;
}
